$color_1: #a4252c;
$color_2: #605e5c;
$color_3: #323130;
$color_primary: var(--primary-color);
$color_primary_text: #ffffff;
$color_6: #005a9e;
$color_7: #d45c00;
$color_8: #00b7c3;
$color_9: #498205;
$color_10: #ffaa44;
$color_11: #8378de;
$color_12: #d13438;
$color_13: #b4aba3;
$color_14: inherit;
$color_15: #107c10;
$color_16: #797775;
$color_17: #a80000;
$color_18: #faf9f8;
$color_19: #f8f9fa;
$color_20: #a19f9d;
$font-family_1: var(--font-family);
$font-family_2: "PrimeIcons";
$background-color_1: rgba(0, 0, 0, 0.4);
$background-color_2: #faf9f8;
$background-color_3: transparent;
$background-color_primary: var(--primary-color);
$background-color_5: #ffffff;
$background-color_6: #c8c6c4;
$background-color_7: #a19f9d;
$background-color_8: rgba(0, 0, 0, 0.5);
$background-color_9: rgba(255, 255, 255, 0.1);
$background-color_10: #edebe9;
$background-color_11: #498205;
$background-color_12: #00b7c3;
$background-color_13: #ffaa44;
$background-color_14: #d13438;
$background-color_15: #d45c00;
$background-color_16: #deecf9;
$background-color_17: #323130;
$background-color_18: #f3f2f1;
$border-color_1: #323130;
$border-color_2: var(--primary-color);
$border-color_3: #a4252c;
$border-color_4: transparent;
$border-color_5: #005a9e;
$border-color_6: #ffffff;
$border-color_7: #605e5c;
$border-color_8: #106ebe;
$border-color_9: #bf5300;
$border-color_10: #aa4a00;
$border-color_11: #00a5b0;
$border-color_12: #00929c;
$border-color_13: #427505;
$border-color_14: #3a6804;
$border-color_15: #ff9b24;
$border-color_16: #ff8d03;
$border-color_17: #6a5dd7;
$border-color_18: #5141d1;
$border-color_19: #c02b2f;
$border-color_20: #aa272a;
$border-color_21: #a19f9d;
$border-color_22: #edebe9;
$border-color_23: transparent transparent transparent transparent;
$border-color_24: rgba(255, 255, 255, 0);
$border-color_25: #eeeeee;
$border-color_26: #f3f2f1;
$border-top-color_1: #ffffff;
$border-right-color_1: #ffffff;
$border-bottom-color_1: #ffffff;
$border-bottom-color_2: #f2f2f2;
$border-left-color_1: #ffffff;
$outline-color_1: #605e5c;

@keyframes p-component-overlay-enter-animation {
	from {
		background-color: transparent;
	}
	to {
		background-color: var(--maskbg);
	}
}
@keyframes p-component-overlay-leave-animation {
	from {
		background-color: var(--maskbg);
	}
	to {
		background-color: transparent;
	}
}

* {
	box-sizing: border-box;
}
.p-component {
	font-family: $font-family_1;
	font-size: 1rem;
	font-weight: normal;
	&:disabled {
		opacity: 0.6;
	}
	.p-menu-separator {
		border-color: $border-color_25;
	}
}
.p-component-overlay {
	background-color: $background-color_1;
	transition-duration: 0.2s;
}
.p-disabled {
	opacity: 0.6;
}
.p-error {
	color: $color_1;
}
.p-text-secondary {
	color: $color_2;
}
.pi {
	font-size: 1rem;
}
.p-link {
	font-size: 1rem;
	font-family: $font-family_1;
	border-radius: 2px;
	&:focus {
		outline: 0 none;
		outline-offset: 0;
		box-shadow: inset 0 0 0 1px #605e5c;
	}
}
.p-component-overlay-enter {
	animation: p-component-overlay-enter-animation 150ms forwards;
}
.p-component-overlay-leave {
	animation: p-component-overlay-leave-animation 150ms forwards;
}
.p-autocomplete {
	.p-autocomplete-loader {
		right: 0.5rem;
	}
	.p-autocomplete-multiple-container {
		padding: 0.25rem 0.5rem;
		&:not(.p-disabled) {
			&:hover {
				border-color: $border-color_1;
			}
		}
		&:not(.p-disabled).p-focus {
			outline: 0 none;
			outline-offset: 0;
			box-shadow: inset 0 0 0 1px #605e5c;
			border-color: $border-color_2;
		}
		.p-autocomplete-input-token {
			padding: 0.25rem 0;
			input {
				font-family: $font-family_1;
				font-size: 1rem;
				color: $color_3;
				padding: 0;
				margin: 0;
			}
		}
		.p-autocomplete-token {
			padding: 0.25rem 0.5rem;
			margin-right: 0.5rem;
			background: $background-color_10;
			color: $color_3;
			border-radius: 2px;
			.p-autocomplete-token-icon {
				margin-left: 0.5rem;
			}
		}
	}
}
.p-autocomplete.p-autocomplete-dd {
	.p-autocomplete-loader {
		right: 2.857rem;
	}
}
.p-autocomplete.p-invalid.p-component {
	>.p-inputtext {
		border-color: $border-color_3;
	}
}
.p-autocomplete-panel {
	background: #ffffff;
	color: $color_3;
	border: 0 none;
	border-radius: 2px;
	box-shadow: rgba(0, 0, 0, 0.133) 0px 3.2px 7.2px 0px, rgba(0, 0, 0, 0.11) 0px 0.6px 1.8px 0px;
	.p-autocomplete-items {
		padding: 0;
		.p-autocomplete-item {
			margin: 0;
			padding: 0.75rem 0.5rem;
			border: 1px solid transparent;
			color: $color_3;
			background: transparent;
			transition: box-shadow 0.2s;
			border-radius: 0;
			&:hover {
				color: $color_3;
				background: #f3f2f1;
			}
		}
		.p-autocomplete-item.p-highlight {
			color: $color_3;
			background: $background-color_10;
		}
		.p-autocomplete-item-group {
			margin: 0;
			padding: 0.75rem 0.5rem;
			color: $color_primary;
			background: #ffffff;
			font-weight: 600;
		}
	}
}
.p-calendar.p-invalid.p-component {
	>.p-inputtext {
		border-color: $border-color_3;
	}
}
.p-datepicker {
	padding: 0.75rem;
	background: #ffffff;
	color: $color_3;
	border: 1px solid #605e5c;
	border-radius: 2px;
	&:not(.p-datepicker-inline) {
		background: #ffffff;
		border: 0 none;
		box-shadow: rgba(0, 0, 0, 0.133) 0px 3.2px 7.2px 0px, rgba(0, 0, 0, 0.11) 0px 0.6px 1.8px 0px;
		.p-datepicker-header {
			background: #ffffff;
		}
	}
	.p-datepicker-header {
		padding: 0 0.5rem 0.5rem 0.5rem;
		color: $color_3;
		background: #ffffff;
		font-weight: 600;
		margin: 0;
		border-bottom: 0 none;
		border-top-right-radius: 2px;
		border-top-left-radius: 2px;
		.p-datepicker-prev {
			width: 2rem;
			height: 2rem;
			color: $color_2;
			border: 0 none;
			background: transparent;
			border-radius: 2px;
			transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
			&:enabled {
				&:hover {
					color: $color_2;
					border-color: $border-color_4;
					background: #f3f2f1;
				}
			}
			&:focus {
				outline: 0 none;
				outline-offset: 0;
				box-shadow: inset 0 0 0 1px #605e5c;
			}
		}
		.p-datepicker-next {
			width: 2rem;
			height: 2rem;
			color: $color_2;
			border: 0 none;
			background: transparent;
			border-radius: 2px;
			transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
			&:enabled {
				&:hover {
					color: $color_2;
					border-color: $border-color_4;
					background: #f3f2f1;
				}
			}
			&:focus {
				outline: 0 none;
				outline-offset: 0;
				box-shadow: inset 0 0 0 1px #605e5c;
			}
		}
		.p-datepicker-title {
			line-height: 2rem;
			order: 1;
			margin: 0 auto 0 0;
			.p-datepicker-year {
				color: $color_3;
				transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
				font-weight: 600;
				padding: 0.5rem;
				&:enabled {
					&:hover {
						color: $color_primary;
					}
				}
			}
			.p-datepicker-month {
				color: $color_3;
				transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
				font-weight: 600;
				padding: 0.5rem;
				margin-right: 0.5rem;
				&:enabled {
					&:hover {
						color: $color_primary;
					}
				}
			}
		}
	}
	table {
		font-size: 1rem;
		margin: 0;
		font-size: 90%;
		th {
			padding: 0.5rem;
			>span {
				width: 2rem;
				height: 2rem;
			}
		}
		td {
			padding: 1px;
			>span {
				width: 2rem;
				height: 2rem;
				border-radius: 2px;
				transition: box-shadow 0.2s;
				border: 0 none;
				&:focus {
					outline: 0 none;
					outline-offset: 0;
					box-shadow: inset 0 0 0 1px #605e5c;
				}
			}
			>span.p-highlight {
				color: $color_3;
				background: $background-color_10;
			}
		}
		td.p-datepicker-today {
			>span {
				background: $background-color_primary;
				color: $color_primary_text;
				border-color: $border-color_4;
				border-radius: 50%;
			}
			>span.p-highlight {
				color: $color_3;
				background: $background-color_10;
			}
		}
	}
	.p-datepicker-buttonbar {
		padding: 1rem 0;
		border-top: 1px solid $border-color_22;
		border-top: 0 none;
		.p-button {
			width: auto;
			font-size: 90%;
			color: $color_3;
			.p-button-label {
				font-weight: 400;
			}
			&:enabled {
				&:hover {
					color: $color_3;
					background-color: $background-color_18;
				}
			}
		}
	}
	.p-timepicker {
		border-top: 1px solid $border-color_22;
		padding: 0.5rem;
		border-top: 0 none;
		button {
			width: 2rem;
			height: 2rem;
			color: $color_2;
			border: 0 none;
			background: transparent;
			border-radius: 2px;
			transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
			&:enabled {
				&:hover {
					color: $color_2;
					border-color: $border-color_4;
					background: #f3f2f1;
				}
			}
			&:focus {
				outline: 0 none;
				outline-offset: 0;
				box-shadow: inset 0 0 0 1px #605e5c;
			}
			&:last-child {
				margin-top: 0.2em;
			}
		}
		span {
			font-size: 1.25rem;
			font-size: 90%;
		}
		>div {
			padding: 0 0.5rem;
		}
		.pi-chevron-up {
			&:before {
				content: "\e91c";
			}
		}
		.pi-chevron-down {
			&:before {
				content: "\e919";
			}
		}
	}
	.p-monthpicker {
		margin: 0;
		.p-monthpicker-month {
			padding: 1px;
			transition: box-shadow 0.2s;
			border-radius: 2px;
			padding: 0.5rem 0;
		}
		.p-monthpicker-month.p-highlight {
			color: $color_3;
			background: $background-color_10;
		}
	}
	.p-yearpicker {
		margin: 0;
		.p-yearpicker-year {
			padding: 1px;
			transition: box-shadow 0.2s;
			border-radius: 2px;
		}
		.p-yearpicker-year.p-highlight {
			color: $color_3;
			background: $background-color_10;
		}
	}
	&:not(.p-disabled) {
		table {
			td {
				span {
					&:not(.p-highlight) {
						&:not(.p-disabled) {
							&:hover {
								background: #f3f2f1;
							}
							&:focus {
								outline: 0 none;
								outline-offset: 0;
								box-shadow: inset 0 0 0 1px #605e5c;
							}
						}
					}
				}
			}
		}
		.p-monthpicker {
			.p-monthpicker-month {
				&:not(.p-disabled) {
					&:not(.p-highlight) {
						&:hover {
							background: #f3f2f1;
						}
					}
					&:focus {
						outline: 0 none;
						outline-offset: 0;
						box-shadow: inset 0 0 0 1px #605e5c;
					}
				}
			}
		}
		.p-yearpicker {
			.p-yearpicker-year {
				&:not(.p-disabled) {
					&:not(.p-highlight) {
						&:hover {
							background: #f3f2f1;
						}
					}
					&:focus {
						outline: 0 none;
						outline-offset: 0;
						box-shadow: inset 0 0 0 1px #605e5c;
					}
				}
			}
		}
	}
	.p-datepicker-prev {
		order: 2;
		.p-datepicker-prev-icon {
			&:before {
				content: "\e919";
			}
		}
	}
	.p-datepicker-next {
		order: 3;
		font-size: 90%;
		.p-datepicker-next-icon {
			&:before {
				content: "\e91c";
			}
		}
	}
}
.p-datepicker.p-datepicker-timeonly {
	.p-timepicker {
		border-top: 0 none;
	}
}
.p-datepicker.p-datepicker-multiple-month {
	.p-datepicker-group {
		border-left: 1px solid $border-color_22;
		padding-right: 0.75rem;
		padding-left: 0.75rem;
		padding-top: 0;
		padding-bottom: 0;
		&:first-child {
			padding-left: 0;
		}
		&:last-child {
			padding-right: 0;
			border-left: 0 none;
		}
	}
}
.p-cascadeselect {
	background: #ffffff;
	border: 1px solid #605e5c;
	transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
	border-radius: 2px;
	&:not(.p-disabled) {
		&:hover {
			border-color: $border-color_1;
		}
	}
	&:not(.p-disabled).p-focus {
		outline: 0 none;
		outline-offset: 0;
		box-shadow: inset 0 0 0 1px #605e5c;
		border-color: $border-color_2;
	}
	.p-cascadeselect-label {
		background: transparent;
		border: 0 none;
		padding: 0.5rem 0.5rem;
		&:enabled {
			&:focus {
				outline: 0 none;
				box-shadow: none;
			}
		}
	}
	.p-cascadeselect-label.p-placeholder {
		color: $color_2;
	}
	.p-cascadeselect-trigger {
		background: transparent;
		color: $color_2;
		width: 2.357rem;
		border-top-right-radius: 2px;
		border-bottom-right-radius: 2px;
	}
}
.p-cascadeselect.p-invalid.p-component {
	border-color: $border-color_3;
}
.p-cascadeselect-panel {
	background: #ffffff;
	color: $color_3;
	border: 0 none;
	border-radius: 2px;
	box-shadow: rgba(0, 0, 0, 0.133) 0px 3.2px 7.2px 0px, rgba(0, 0, 0, 0.11) 0px 0.6px 1.8px 0px;
	.p-cascadeselect-items {
		padding: 0;
		.p-cascadeselect-item {
			margin: 0;
			border: 1px solid transparent;
			color: $color_3;
			background: transparent;
			transition: box-shadow 0.2s;
			border-radius: 0;
			.p-cascadeselect-item-content {
				padding: 0.75rem 0.5rem;
				&:focus {
					outline: 0 none;
					outline-offset: 0;
					box-shadow: inset 0 0 0 0.15rem #605e5c;
				}
			}
			&:not(.p-highlight) {
				&:not(.p-disabled) {
					&:hover {
						color: $color_3;
						background: #f3f2f1;
					}
				}
			}
			.p-cascadeselect-group-icon {
				font-size: 0.875rem;
			}
		}
		.p-cascadeselect-item.p-highlight {
			color: $color_3;
			background: $background-color_10;
		}
	}
}
.p-input-filled {
	.p-cascadeselect {
		background: #faf9f8;
		&:not(.p-disabled) {
			&:hover {
				background-color: $background-color_2;
			}
		}
		&:not(.p-disabled).p-focus {
			background-color: $background-color_2;
		}
	}
	.p-checkbox {
		.p-checkbox-box {
			background-color: $background-color_2;
		}
		.p-checkbox-box.p-highlight {
			background: $background-color_primary;
		}
		&:not(.p-checkbox-disabled) {
			.p-checkbox-box {
				&:hover {
					background-color: $background-color_2;
				}
			}
			.p-checkbox-box.p-highlight {
				&:hover {
					background: #005a9e;
				}
			}
		}
	}
	.p-dropdown {
		background: #faf9f8;
		&:not(.p-disabled) {
			&:hover {
				background-color: $background-color_2;
			}
		}
		&:not(.p-disabled).p-focus {
			background-color: $background-color_2;
			.p-inputtext {
				background-color: $background-color_3;
			}
		}
	}
	.p-inputtext {
		background-color: $background-color_2;
		&:enabled {
			&:hover {
				background-color: $background-color_2;
			}
			&:focus {
				background-color: $background-color_2;
			}
		}
	}
	.p-multiselect {
		background: #faf9f8;
		&:not(.p-disabled) {
			&:hover {
				background-color: $background-color_2;
			}
		}
		&:not(.p-disabled).p-focus {
			background-color: $background-color_2;
		}
	}
	.p-radiobutton {
		.p-radiobutton-box {
			background-color: $background-color_2;
			&:not(.p-disabled) {
				&:hover {
					background-color: $background-color_2;
				}
			}
		}
		.p-radiobutton-box.p-highlight {
			background: #ffffff;
			&:not(.p-disabled) {
				&:hover {
					background: #ffffff;
				}
			}
		}
	}
	.p-treeselect {
		background: #faf9f8;
		&:not(.p-disabled) {
			&:hover {
				background-color: $background-color_2;
			}
		}
		&:not(.p-disabled).p-focus {
			background-color: $background-color_2;
		}
	}
}
.p-checkbox {
	width: 20px;
	height: 20px;
	.p-checkbox-box {
		border: 1px solid #605e5c;
		background: #ffffff;
		width: 20px;
		height: 20px;
		color: $color_3;
		border-radius: 2px;
		transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
		.p-checkbox-icon {
			transition-duration: 0.2s;
			color: $color_primary_text;
			font-size: 14px;
		}
		&:not(.p-disabled).p-focus {
			box-shadow: inset 0 0 0 1px $background-color_primary;
		}
	}
	.p-checkbox-box.p-highlight {
		border-color: $border-color_2;
		background: $background-color_primary;
		&:not(.p-disabled) {
			&:hover {
				border-color: $border-color_5;
				background: #005a9e;
				color: $color_primary_text;
			}
		}
	}
	&:not(.p-checkbox-disabled) {
		.p-checkbox-box {
			&:hover {
				border-color: $border-color_1;
			}
		}
		.p-checkbox-box.p-focus {
			outline: 0 none;
			outline-offset: 0;
			box-shadow: inset 0 0 0 1px #605e5c;
			border-color: $border-color_2;
		}
		.p-checkbox-box.p-highlight {
			&:hover {
				border-color: $border-color_5;
				background: #005a9e;
				color: $color_primary_text;
			}
		}
	}
	.p-checkbox-box.p-disabled {
		background-color: $background-color_18;
		border-color: $border-color_26;
		color: $color_20;
		opacity: 1;
		user-select: none;
	}
}
.p-checkbox.p-invalid {
	>.p-checkbox-box {
		border-color: $border-color_3;
	}
}
.p-chips {
	.p-chips-multiple-container {
		padding: 0.25rem 0.5rem;
		&:not(.p-disabled) {
			&:hover {
				border-color: $border-color_1;
			}
		}
		&:not(.p-disabled).p-focus {
			outline: 0 none;
			outline-offset: 0;
			box-shadow: inset 0 0 0 1px #605e5c;
