	.p-menubar {
		position: relative;
		.p-menubar-button {
			display: flex;
			width: 2rem;
			height: 2rem;
			color: $color_primary_text;
			border-radius: 2px;
			transition: background-color 0.2s, color 0.2s, box-shadow 0.2s;
			&:hover {
				color: $color_primary_text;
				background: $background-color_primary;
			}
			&:focus {
				outline: 0 none;
				outline-offset: 0;
				box-shadow: inset 0 0 0 1px #605e5c;
			}
		}
		.p-menubar-root-list {
			position: absolute;
			display: none;
			padding: 0;
			background: #ffffff;
			border: 0 none;
			box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
			width: 100%;
			.p-menu-separator {
				border-top: 1px solid $border-color_22;
				margin: 0.25rem 0;
			}
			.p-submenu-icon {
				font-size: 0.875rem;
			}
			>.p-menuitem {
				width: 100%;
				position: static;
				>.p-menuitem-link {
					padding: 0.75rem 0.5rem;
					color: $color_3;
					border-radius: 0;
					transition: box-shadow 0.2s;
					user-select: none;
					&.p-menuitem-active {
						background-color: $background-color_10;
						color: $color_primary_text;
						.p-menuitem-text {
							color: $color_primary;
						}
					}
					.p-menuitem-text {
						color: $color_3;
					}
					.p-menuitem-icon {
						color: $color_primary;
						margin-right: 0.5rem;
					}
					.p-submenu-icon {
						color: $color_primary;
					}
					&:not(.p-disabled) {
						&:hover {
							background: #f3f2f1;
							.p-menuitem-text {
								color: $color_3;
							}
							.p-menuitem-icon {
								color: $color_primary;
							}
							.p-submenu-icon {
								color: $color_primary;
							}
						}
					}
					&:focus {
						outline: 0 none;
						outline-offset: 0;
						box-shadow: inset 0 0 0 0.15rem #605e5c;
					}
					>.p-submenu-icon {
						margin-left: auto;
						transition: transform 0.2s;
					}
				}
			}
			>.p-menuitem.p-menuitem-active {
				>.p-menuitem-link {
					>.p-submenu-icon {
						transform: rotate(-180deg);
					}
				}
			}
			.p-submenu-list {
				width: 100%;
				position: static;
				box-shadow: none;
				border: 0 none;
				.p-submenu-icon {
					transition: transform 0.2s;
					transform: rotate(90deg);
				}
				.p-menuitem-active {
					>.p-menuitem-link {
						>.p-submenu-icon {
							transform: rotate(-90deg);
						}
					}
				}
			}
			.p-menuitem {
				width: 100%;
				position: static;
			}
			ul {
