

.register-form .card {
    width: 450px;
}
.register-form .card form {
    margin-top: 2rem;
}
.register-form .card .field {
    margin-bottom: 1.5rem;
}
@media screen and (max-width: 960px) {
    .register-form .card {
        width: 80%;
    }
}
@media screen and (max-width: 640px) {
    .register-form .card {

        width: 90%;
        min-width: 0;
    }
}