@import './variables';


:root {
    --surface-a: #bebbb8;

    --primary-color: #a26360;
    --primary-color-text: #ffffff;
    --text-color: #495057;
    --font-family: "Roboto", sans-serif;
    --bluegray-50: #f8f9fb;
    --bluegray-100: #dee4ed;
    --bluegray-200: #c4cfe0;
    --bluegray-300: #a9bad2;
    --bluegray-400: #8fa4c4;
    --bluegray-500: #758fb6;
    --bluegray-600: #637a9b;
    --bluegray-700: #52647f;
    --bluegray-800: #404f64;
    --bluegray-900: #2f3949;

    --content-padding: 1rem;
    --inline-spacing: 0.5rem;
    --border-radius: 2px;
    --surface-ground: #faf9f8;
    --surface-section: #ffffff;
    --surface-card: #ffffff;
    --surface-overlay: #ffffff;
    --surface-border: #edebe9;
    --surface-hover: #f3f2f1;
    --maskbg: rgba(0, 0, 0, 0.4);
    --focus-ring: inset 0 0 0 1px #605e5c;
    color-scheme: light;
}

.fluent-theme-dark {
    // --primary-color: $light-primary-color;
    // --primary-color-text: $light-text-color;
    // --font-family: $font-family;
    // --surface-a: $light-bg-color;
    --surface-a: #bebbb8;

    --primary-color: #84342d;
    --primary-color-text: #ffffff;
}

body {
    background-color: var(--surface-a);
    font-family: var(--font-family);
    color: var(--primary-color-text);
}

.p-menuitem-icon-no-margin {
    margin-right: 0rem !important;
}

.card {
    background: var(--surface-card);
    padding: 1rem;
    border-radius: 10px;
    margin-bottom: 2rem;
}

.custom-skeleton {
    border: 1px solid var(--surface-d) !important;
    border-radius: 4px !important;
}
.custom-skeleton ul {
    list-style: none;
}


.p-menu .p-menuitem-link:focus {
    outline: 0 none;
    outline-offset: 0;
    box-shadow: none;
}


.p-datatable {
    .p-datatable-header {
        background: #ffffff;
        // color: $color_3;
        border: 1px solid #f3f2f1;
        border-width: 1px 0 1px 0;
        padding: 0.75rem 0.75rem !important;
        font-weight: 600;
    }
}

.center-content {
    position: relative;
    float: left;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.body-static-height {
    height: calc(100vh - 64px);
    overflow-y: auto;
}

/***********Scroll bars*************/

/* width and Height*/
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: gray;
}

.project-page-tags {
    background-color: rgb(237, 235, 235) !important;
    color: black !important;
    border-radius: 4px !important;
    display: block !important;
    text-align: center !important;
    word-wrap: normal !important;
    white-space: nowrap !important;
    text-overflow: ellipsis !important;
    overflow: hidden !important;
    cursor: pointer !important;
    width: 100% !important;
    font-weight: 400 !important;
    border: none !important;
    text-shadow: none !important;
    padding: 15px 0 16px !important;
    margin-top: 15px !important;
    font-size: 13px !important;
}

.custom-tabview{
    background-color: greenyellow !important;
    .p-unselectable-text .p-tabview-selected .p-highlight{
        .p-tabview-nav-link{
            background-color: red !important;
        }
    }
}